@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap);
a {
    text-decoration: none;
    color: inherit;
}



.movie img{
    position: relative;
    top: -50px;
    max-width: 200px;
    margin-top: 10px;
    margin-left: 10px;
}

.movie .movie_data{
    margin-left: 20px;
}

.movie .movie_title{
    font-size: 28pt;
    font-weight: 1000;
    margin: 10px;
    margin-left: 0;
}

.movie .movie_year{
    margin: 10px;
    padding: 0;
    margin-left: 0;
}

.movie .movie_genres{
    list-style: none;
    color: red;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 10px;
    margin-left: 0px;
    font-size: 20px;
}


.movie .movie_genres li{
    margin-right: 10px;
};
#root{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

html,
body,
.container{
    height: 100%;
    display:flex;
    justify-content: center;
}

.Logo{
    color: red;
    font-family: 'Fjalla One', sans-serif;
    font-size: 40pt;
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 25px;
    font-weight: 900;
}


.movies{
    display: flex;
    flex-direction: column;
    padding: 50px;
    justify-content: space-between;
    align-items: center;
    margin: 5%;
    width: 80%;
}

.movies .movie{
    width: 800px;
    background-color: #e9ecef;
    margin-bottom: 40px;
    margin-top: 40px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 900;
    border-radius: 5px;
}




.detail_container{
    width: 100%;
    height: 100%;
    display:flex;
    justify-content: center;
    align-items: flex-start;
}



.detail_movie{
    width: 80%;
    background-color: #e9ecef;
    margin-bottom: 40px;
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 900;
    border-radius: 5px;
    align-items: center;
}

.box{
    display: flex;

}



.detail_movie img{
    position: relative;
    top: -50px;
    max-width: 200px;
    margin-top: 10px;
    margin-left: 10px;
}

.detail_movie .detail_movie_data{
    margin-left: 20px;
}

.detail_movie .detail_movie_title{
    font-size: 28pt;
    font-weight: 1000;
    margin: 10px;
    margin-left: 0;
}

.detail_movie .detail_movie_year{
    margin: 10px;
    padding: 0;
    margin-left: 0;
}

.detail_movie .detail_movie_genres{
    list-style: none;
    color: red;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 10px;
    margin-left: 0px;
    font-size: 20px;
}


.detail_movie .detail_movie_genres li{
    margin-right: 10px;
};

.detail_movie_trailer{
    display: block;
    position: relative;
    left: -50px;
}
.nav{
    position: fixed;
    top: 10px;
    right: 10px;
    display: flex;
    background-color: #e9ecef;
    padding: 10px 20px;
    border-radius: 5px;

}

.nav a{
    text-decoration: none;
    color: red;
    text-transform: uppercase;
    -webkit-text-decoration-line: center;
            text-decoration-line: center;
    font-weight: 900;
}

.nav a:not(:last-child){
    /* margin-bottom: 20px; */
}
body{
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    background-color: black;
    height: 100%;
}
.logo{
    color: red;
    text-align: center;
    display: block;
}
