@import url('https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap');

#root{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

html,
body,
.container{
    height: 100%;
    display:flex;
    justify-content: center;
}

.Logo{
    color: red;
    font-family: 'Fjalla One', sans-serif;
    font-size: 40pt;
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 25px;
    font-weight: 900;
}


.movies{
    display: flex;
    flex-direction: column;
    padding: 50px;
    justify-content: space-between;
    align-items: center;
    margin: 5%;
    width: 80%;
}

.movies .movie{
    width: 800px;
    background-color: #e9ecef;
    margin-bottom: 40px;
    margin-top: 40px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 900;
    border-radius: 5px;
}


