a {
    text-decoration: none;
    color: inherit;
}



.movie img{
    position: relative;
    top: -50px;
    max-width: 200px;
    margin-top: 10px;
    margin-left: 10px;
}

.movie .movie_data{
    margin-left: 20px;
}

.movie .movie_title{
    font-size: 28pt;
    font-weight: 1000;
    margin: 10px;
    margin-left: 0;
}

.movie .movie_year{
    margin: 10px;
    padding: 0;
    margin-left: 0;
}

.movie .movie_genres{
    list-style: none;
    color: red;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 10px;
    margin-left: 0px;
    font-size: 20px;
}


.movie .movie_genres li{
    margin-right: 10px;
};