
.detail_container{
    width: 100%;
    height: 100%;
    display:flex;
    justify-content: center;
    align-items: flex-start;
}



.detail_movie{
    width: 80%;
    background-color: #e9ecef;
    margin-bottom: 40px;
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 900;
    border-radius: 5px;
    align-items: center;
}

.box{
    display: flex;

}



.detail_movie img{
    position: relative;
    top: -50px;
    max-width: 200px;
    margin-top: 10px;
    margin-left: 10px;
}

.detail_movie .detail_movie_data{
    margin-left: 20px;
}

.detail_movie .detail_movie_title{
    font-size: 28pt;
    font-weight: 1000;
    margin: 10px;
    margin-left: 0;
}

.detail_movie .detail_movie_year{
    margin: 10px;
    padding: 0;
    margin-left: 0;
}

.detail_movie .detail_movie_genres{
    list-style: none;
    color: red;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 10px;
    margin-left: 0px;
    font-size: 20px;
}


.detail_movie .detail_movie_genres li{
    margin-right: 10px;
};

.detail_movie_trailer{
    display: block;
    position: relative;
    left: -50px;
}